import {runtime, storage} from 'webextension-polyfill'
import {injectScript} from "../utils/inject-func";
import {dispatchCustomEvent, listenEvent} from "../utils/event-util";


injectScript('inject-scripts/initFrame.js');

/**
 * wapi 注入成功后 会询问是否要验证锁屏事件？
 */
window.addEventListener('startLockVerify', async (detail) => {
    const url = runtime.getURL('images/avatar.svg');
    console.log(url);
    const host = url.replace('images/avatar.svg', '')
    console.log(host);
    dispatchCustomEvent('chromeExtensionHost', {host: host});

    const data = await storage.sync.get(['on', 'lockScreen', 'lockPassword']);
    console.log(data);
    if (data?.on && data?.lockScreen && data?.lockPassword) {
        console.log(data)
        dispatchCustomEvent('startLockScreen', data)
    }
})

// 页面加载的时候，我们加载一下对应的插件内容
runtime.sendMessage({zbaseType: 'zbase-content-init'});
listenEvent('forgetLockPasswd', (data) => {
    console.log(data);
    storage.sync.set({'lockPassword': data.passwd});
});

runtime.onMessage.addListener((data) => {
    // 处理部分popup发送的请求
    function joinGroup(data) {
        let aLink = document.createElement('a')
        aLink.href = data.message.url
        document.body.appendChild(aLink)
        aLink.click()
        document.body.removeChild(aLink)
    }
    if (data.from === 'popup') {
        switch(data.id) {
            case 'joinGroup':
                joinGroup(data)
                break
        }
        return true
    }
})





